import React, { useState, useEffect } from 'react';
import { MDBInput, MDBBtn } from "mdbreact";
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';
import fede from '../img/profile/fede.jpg';

const UserProfile = () => {
    const [form, setValues] = useState({
        complete_name: 'Federico Brandón',
        cellphone: '091225679',
        age: '22',
        weight: '60',
        height: '160',
        email: 'el_cara@tna.com.uy',
        password: '****',
        administrator: true
    });

    const handleInput = e => {
        setValues({
            ...form,
            [e.target.name]: e.target.value
        });
    }

    const sendForm = () => {
        error();
        Object.keys(userData).forEach(key => {
            console.log(`${key} : ${userData[key]}`);
            setValues({
                ...form,
                key: userData[key]
            });
        });
    }

    const error = () => {
        const Toast = Swal.mixin({
            toast: true,
            position: 'top-end',
            showConfirmButton: false,
            timer: 3000
        })
        Toast.fire({
            icon: 'warning',
            title: 'Falta terminar Fede!'
        })
    }

    const userData = {
        complete_name: 'Federico Brandón',
        cellphone: '091225679',
        age: '22',
        weight: '60kg + 5 Donas',
        height: 'Petiso',
        email: 'el_cara@tna.com.uy',
        password: '****',
        administrator: true
    }


    return (
        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <img style={{ width: '150px', borderRadius: '50%', marginBottom: '1rem' }} src={fede} alt="profile-image"></img>
            <form style={{ width: '100%'}}>
                <div className="grey-text">
                    <MDBInput
                        style={{ color: 'white' }}
                        label="Nombre y apellido"
                        icon="file-signature"
                        group
                        type="text"
                        validate
                        error="wrong"
                        success="right"
                        name="complete_name"
                        value={form.complete_name}
                        onInput={handleInput}
                    />
                    <MDBInput
                        style={{ color: 'white' }}
                        label="Email"
                        icon="envelope"
                        group
                        type="email"
                        validate
                        error="wrong"
                        success="right"
                        name="email"
                        value={form.email}
                        onInput={handleInput}
                    />
                    <MDBInput
                        style={{ color: 'white' }}
                        label="Contraseña"
                        icon="key"
                        group
                        type="password"
                        validate
                        error="wrong"
                        success="right"
                        name="password"
                        value={form.password}
                        onInput={handleInput}
                    />
                    <MDBInput
                        style={{ color: 'white' }}
                        label="Celular"
                        icon="mobile-alt"
                        group
                        type="number"
                        validate
                        error="wrong"
                        success="right"
                        name="cellphone"
                        value={form.cellphone}
                        onInput={handleInput}
                    />
                    <MDBInput
                        style={{ color: 'white' }}
                        label="Edad"
                        icon="file-signature"
                        group
                        type="number"
                        validate
                        error="wrong"
                        success="right"
                        name="age"
                        value={form.age}
                        onInput={handleInput}
                    />
                    <MDBInput
                        style={{ color: 'white' }}
                        label="Peso"
                        icon="weight"
                        group
                        type="number"
                        validate
                        error="wrong"
                        success="right"
                        name="weight"
                        value={form.weight}
                        onInput={handleInput}
                    />
                    <MDBInput
                        style={{ color: 'white' }}
                        label="Altura"
                        icon="long-arrow-alt-up"
                        group
                        type="number"
                        validate
                        error="wrong"
                        success="right"
                        name="height"
                        value={form.height}
                        onInput={handleInput}
                    />
                </div>
                <div className="text-center py-4 mt-3">
                    <MDBBtn color="success" onClick={sendForm}>Guardar cambios</MDBBtn>
                </div>
            </form>
        </div>
    );

}


export default UserProfile;
