import React, { useState } from 'react';
import { MDBInput, MDBBtn } from "mdbreact";
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';

const Signup = () => {

    const [form, setValues] = useState({
        complete_name: '',
        cellphone: '',
        age: '',
        weight: '',
        height: '',
        email: '',
        password: '',
        administrator: false
    });

    const handleInput = e => {
        setValues({
            ...form,
            [e.target.name]: e.target.value
        });
    }

    const sendForm = () => {
        error();
    }

    const error = () => {
        const Toast = Swal.mixin({
            toast: true,
            position: 'top-end',
            showConfirmButton: false,
            timer: 3000
        })
        Toast.fire({
            icon: 'warning',
            title: 'Falta terminar Fede!'
        })
    }

    return (
        <form>
            <p className="h4 text-center py-4 grey-text">Registro</p>
            <div className="grey-text">
                <MDBInput
                    style={{ color: 'white' }}
                    label="Nombre y apellido"
                    icon="file-signature"
                    group
                    type="text"
                    validate
                    error="wrong"
                    success="right"
                    name="complete_name"
                    value={form.complete_name}
                    onInput={handleInput}
                />
                <MDBInput
                    style={{ color: 'white' }}
                    label="Email"
                    icon="envelope"
                    group
                    type="email"
                    validate
                    error="wrong"
                    success="right"
                    name="email"
                    value={form.email}
                    onInput={handleInput}
                />
                <MDBInput
                    style={{ color: 'white' }}
                    label="Contraseña"
                    icon="key"
                    group
                    type="password"
                    validate
                    error="wrong"
                    success="right"
                    name="password"
                    value={form.password}
                    onInput={handleInput}
                />
                <MDBInput
                    style={{ color: 'white' }}
                    label="Celular"
                    icon="mobile-alt"
                    group
                    type="number"
                    validate
                    error="wrong"
                    success="right"
                    name="cellphone"
                    value={form.cellphone}
                    onInput={handleInput}
                />
                <MDBInput
                    style={{ color: 'white' }}
                    label="Edad"
                    icon="file-signature"
                    group
                    type="number"
                    validate
                    error="wrong"
                    success="right"
                    name="age"
                    value={form.age}
                    onInput={handleInput}
                />
                <MDBInput
                    style={{ color: 'white' }}
                    label="Peso"
                    icon="weight"
                    group
                    type="number"
                    validate
                    error="wrong"
                    success="right"
                    name="weight"
                    value={form.weight}
                    onInput={handleInput}
                />
                <MDBInput
                    style={{ color: 'white' }}
                    label="Altura"
                    icon="long-arrow-alt-up"
                    group
                    type="number"
                    validate
                    error="wrong"
                    success="right"
                    name="height"
                    value={form.height}
                    onInput={handleInput}
                />
            </div>
            <div className="text-center py-4 mt-3">
                <Link to='/login'><MDBBtn color="deep-orange">Atrás</MDBBtn></Link>
                <MDBBtn color="primary" onClick={sendForm}>Registrarse</MDBBtn>
            </div>
        </form>
    );
}

export default Signup;
