import React, { useState } from 'react';
import { MDBInput, MDBBtn } from "mdbreact";
import { Link, Redirect } from 'react-router-dom';
import Swal from 'sweetalert2';

const Login = () => {

    const [form, setValues] = useState({
        email: '',
        password: '',
    });
    const [loggedIn, setLoggedIn] = useState(false)

    const handleInput = e => {
        setValues({
            ...form,
            [e.target.name]: e.target.value
        });
    }

    const sendForm = () => {
        if (form.email === "fede" && form.password === "1234") {
            setLoggedIn(true);
            localStorage.setItem("user", true);
        } else {
            error();
        }
    }

    const error = () => {
        const Toast = Swal.mixin({
            toast: true,
            position: 'top-end',
            showConfirmButton: false,
            timer: 3000
        })
        Toast.fire({
            icon: 'error',
            title: 'Datos incorrectos'
        })
    }

    return (
        loggedIn ? <Redirect to="/" /> :
            (
                <div>
                    <form>
                        <p className="h4 text-center py-4 grey-text">Iniciar sesión</p>
                        <div className="grey-text">
                            <MDBInput
                                style={{ color: 'white' }}
                                label="Email"
                                icon="envelope"
                                group
                                type="email"
                                validate
                                error="wrong"
                                success="right"
                                name="email"
                                value={form.email}
                                onInput={handleInput}
                            />
                            <MDBInput
                                style={{ color: 'white' }}
                                label="Contraseña"
                                icon="key"
                                group
                                type="password"
                                validate
                                error="wrong"
                                success="right"
                                name="password"
                                value={form.password}
                                onInput={handleInput}
                            />
                        </div>
                        <div className="text-center py-4 mt-3">
                            <Link to='/registrarse'><MDBBtn color="primary">Registrarse</MDBBtn></Link>
                            <MDBBtn color="success" onClick={sendForm}>Ingresar</MDBBtn>
                        </div>
                    </form>
                </div>
            )
    );
}

export default Login;
